@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');
* {
    font-family: 'Lato', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* background-color: #2a2a2a; */
}

html {
    scroll-behavior: smooth;
}

h1, h2 ,h3, h4, h5, h6 {
    color: #242424;
    text-align: left;
    line-height: 1.25;
}


span {
    /* color: #242424; */
    /* text-align: center;
    line-height: 1.25; */
}
.Typewriter  {  
    color: black;
    font-size: 96px;
    font-weight: bold;
}

h1 {
    font-size: 36px;
}

p {
    line-height: 1.5;
    font-size: 16px;
}

a {
    text-decoration: none;
    position: relative;
    transition: opacity 300ms ease;
}

.mini-icons:hover {
    opacity: 0.7;
}

.link__hover-effect:after {
    content: "";
    position: absolute;
    height: 3px;
    width: 0;
    bottom: -3px;
    right: 0;
    transition: all 300ms ease;
}

.link__hover-effect--white::after {
    background-color: #fff;
}
.link__hover-effect--black::after {
    background-color: #000;
}

.link__hover-effect:hover:after {
    width: 100%;
    left: 0;
}

li {
    list-style-type: none;
}

.flex {
    display: flex;
}

.flex-1 {
    flex: 1;
}

.container {
    padding: 50px 0;
}
.row {
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    padding: 0 12px;
}
.row2 {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 12px;
}

.text--purple {
    color: #0000ff;
}

.text--black {
    font-weight: 700;
}

section:nth-child(even) {
    background-color: #f8f8f8;
}

.section__title {
    margin-bottom: 20px;
    text-align: center;
}

/* NAVIGATION BAR */

nav {
    height: 100px;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 12px;
}

.nav__link--list {
    display: flex;
}

.nav__link--anchor {
    margin: 0 12px;
    color: #242424;
    font-weight: 700;
}

.nav__link--anchor-primary {
    background-color: #0000ff;
    padding: 8px 20px;
    border-radius: 50px;
    color: white;
    transition: all 300ms ease;
}

.nav__link--anchor-primary:hover {
    background-color: #000080;
}

.personal__logo {
    font-size: 24px;
    color: #0000ff;
    margin: 0 12px;
    font-weight: bold;
}

/* ABOUT ME */

#about-me {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.about-me__info {
    display: flex;
    flex-direction: column;
}

.im {
    font-size: 40px;
}

.i-title {
    height: 100px;
    overflow: hidden;
    /* text-align: center; */
}
  
.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}
    
@keyframes move {
    25% {
    transform: translateY(-120px);
    }
    50% {
    /* transform: translateY(0); */
    }   
    75% {
    transform: translateY(-240px);
    }
    100% {
    /* transform: translateY(-0); */
    }
}

.i-title-item {
    height: 120px;
    font-size: 72px;
    font-weight: bold;
    color: blue;
    display: flex;
    align-items: center;
    text-align: center;
}

.about-me__info--container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* max-width: 600px; */
    margin: 0 auto;
    text-align: left;
    padding-bottom: 50px;
}

.about-me__picture--mask {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 8px 16px rgba(0, 0,0 ,0.16);
    margin-bottom: 28px;
    animation: animate-profile-picture 800ms 5350ms backwards;
    position: absolute;
    right: -290px;
    top: 0px;
}

@keyframes animate-profile-picture {
    0% {
        transform: scale(0);
    }
    80% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

.about-me__picture {
    width: 100%;

    /* Improving my own picture */
    transform: scale(1.5);
    padding-top: 22px;
}

.about-me__info--title {
    margin-bottom: 16px;
    animation: fade-up 650ms 400ms backwards;
}
.about-me__info--para {
    font-size: 26px;
    margin-bottom: 15px;
    animation: fade-up 650ms 600ms backwards;
    padding-top: 15px;
}

.about-me__link {
    font-size: 20px;
    color: #242424;
    padding: 0 16px;
}

.about-me__links {
    animation: fade-up 650ms 800ms backwards;
}

@keyframes fade-up {
    0% {
        opacity: 0;
        transform: translateY(40px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.about-me__img--container {
    flex: 1;
    display: flex;
    align-items: center;
}
.about-me__img {
    width: 100%;
    animation: fade-in 1200ms 800ms backwards;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.wave {
    display: inline-block;
    animation: animate-wave 500ms infinite ease-in-out;
}

@keyframes animate-wave {
    0% {
        transform: rotate(0);
    }
    50% {
        transform: rotate(30deg);
    }
    100% {
        transform: rotate(0);
    }
}

/* TECH - STACK*/

.language__img {
    width: 100%;
    max-width: 100px;
    transition: all 300ms;
}
.language:hover .language__img {
    filter: brightness(80%);
    opacity: 0.86;
    transform: scale(0.9);
}

.language {
    width: 25%;
    display: flex;
    justify-content: center;
    position: relative;
}
.language__img--wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px 16px;
}

.language__list {
    display: flex;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
}

.language__name {
    position: absolute;
    bottom: 0;
    transform: scale(0);
    transition: all 300ms;
    font-size: 20px;
    opacity: 0;
}
.language:hover .language__name {
    transform: scale(1);
    opacity: 1;
}

/* PROJECTS */

.project {
    margin-bottom: 135px;
}

.project:last-child {
    margin-bottom: 40px;
}

.project__img {
    width: 100%;
    transition: all 500ms ease;
}

.project__wrapper {
    display: flex;
    box-shadow: 0 20px 80px rgba(0, 0,0 ,0.45);
    border-radius: 20px;
    overflow: hidden;
    position: relative;
}
.project__wrapper:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #1c1d25;
    opacity: 0;
    transition: opacity 450ms ease;
    z-index: 2;
}
.project:hover .project__wrapper:before {
    opacity: 0.7;
}
.project:hover .project__img {
    transform: scale(1.07);
    filter: blur(5px);
}

.project:hover .project__description {
    opacity: 1;
    transform: translateY(-50%);
}

.project__list {
    padding-top: 40px;
}

.project__description {
    position: absolute;
    top: 50%;
    left: 90px;
    transform: translateY(100%);
    max-width: 550px;
    z-index: 3;
    opacity: 0;
    transition: transform 450ms, opacity 300ms;
}

.project__description--title {
    font-size: 40px;
}

.project__description--para {
    margin: 16px 0;
}

.project__description--link {
    font-size: 20px;
    margin-right: 16px;
}

.project__description--title,
.project__description--sub-title,
.project__description--para, 
.project__description--link {
    text-align: left;
    color: #fff;
}

/* FOOTER */

footer {
    background-color: #242424;
}

.footer__social--list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 28px;
}
.footer__row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding: 23% 0; */
}

.footer__logo--img {
    width: 70px;
    height: 70px;
}

.footer__social--link,
.footer__copyright,
.footer__logo--popper {
    color: #fff;
}

.footer__logo--popper {
    position: absolute;
    right: 0;
    top: 30px;
    font-weight: 700;
    opacity: 0;
    transition: all 300ms ease;
}

.footer__anchor {
    margin-bottom: 20px;
    position: relative;
}

.footer__anchor:hover .footer__logo--popper {
    transform: translateX(133px);
    opacity: 1;
}

/* Small phones, tablets, large smarthphones */
@media (max-width : 768px) {
    .about-me__picture--mask {
        display: none;
    }
    
    nav {
        height: 68px;
    }

    h1 {
        font-size: 28px;
    }

    .about-me__info--para {
        font-size: 18px;
    }

    .language {
        width: calc(100% / 3);
    }

    .project__description--para {
        font-size: 14px;
    }

    .project__description {
        left: 30px;
        padding-right: 30px;
    }
}

/* SMALL PHONES */
@media (max-width : 480px) {
    .flex-1 {
        position: absolute;
        top: 18%;
    }
    .container {
        padding-top: 0;
        padding-bottom: 50px;
    }
    .improving {
        padding-top: 10px;
        text-align: center;
    }
    .center {
        display: flex;
        align-items: center;
    }
    .about-me__link {
        text-align: center;
    }
    .about-me__info--para{
        line-height: 29px;
    }
    .about-me__info--title {
        padding-top: 35px;
    }
    .about-me__info--container {
        text-align: justify;
        align-items: center;
        padding-bottom: 0px;    
    }
    .about-me__info--container .center {
        align-items: center;
    }
    .personal__logo {
        font-size: 16px;
    }
    .Typewriter {
        font-size: 52px;
    }
    .im {
        font-size: 30px;
    }
    .i-title-item {/*52px*/
        font-size: 46px;
    }
    .about-me__picture--mask {
        display: none;
    }

    .nav__link:not(:last-child) {
        display: none;
    }

    .project__description {
        padding: 0;
        left: 0;
        width: 100%;
    }

    .project__description--para {
        display: none;
    }

    .project__description--links {
        display: flex;
        justify-content: center;
    }

    .project__description--title {
        font-size: 32px;
        line-height: 1;
        text-align: center;
    }

    .project__description--sub-title {
        text-align: center;
        margin: 12px 0;
    }
}

